body {
  background: url(https://images.unsplash.com/photo-1516541196182-6bdb0516ed27?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1834&q=80);
  font-family: "Old Standard TT", "Times New Roman", serif;
  font-size: 18px;
  line-height: 1.5;
  color: rgb(39, 39, 39);
  display: flex;
  justify-content: center;
}

#root {
    width: 600px;
}

div {
    margin-bottom: 5px;
}

input {
  height: 30px;
  width: 230px;
  border-radius: 13px;
  padding-left: 10px;
}

.users {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  color: rgb(44, 28, 83);
}

h2 {
  text-transform: uppercase;
  margin-bottom: 5px;
}

h3 {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 0;
}

p {
  font-size: 20px;
  text-align: left;
}

blockquote {
  margin-top: 5px;
  margin-bottom: 0;
}

.comments {
  width: 90%;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 10px;
  margin: 0 auto;
  margin-bottom: 20px;
}
